import {Link} from "react-router-dom";
import "./Navbar.css";
function Navbar ({onLogout, user}) {
    return (
        <nav className="navbar">
            <div className="logo">LSM </div>
            <div className="menu">
                <Link to="/">Home</Link>
                <Link to="/board"></Link>
                {!user && <Link to="/login"></Link>}
                {!user && <Link to="/signup"></Link>} 
                {user && <span className="username">, {user}!</span>}
                {user && <button onClick={onLogout} className="logout-btn"></button>}
            </div>
        </nav>
    );
}

export default Navbar;