import "./BoardDetail.css";

import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import api from "../api/axiosConfig";

function BoardDetail({ user }) {
  //props user->   username

  const navigate = useNavigate();

  const [post, setPost] = useState(null); //  id   
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [editing, setEditing] = useState(false); //   
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");

  const { id } = useParams(); // board/:id id  

  //  id  1 
  const loadPost = async () => {
    //  id  1 
    try {
      setLoading(true);
      const res = await api.get(`/api/board/${id}`);
      setPost(res.data); //  id  state 
      setTitle(res.data.title);
      //      title  
      setContent(res.data.content);
      //      content  
    } catch (err) {
      console.error(err);
      setError("   .");
      // alert("   .");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadPost(); //  
    loadComments(); //     
  }, [id]);

  //
  const handleDelete = async () => {
    if (!window.confirm(" ?")) {
      //->true, ->false
      return;
    }
    try {
      await api.delete(`/api/board/${id}`);
      alert("  !");
      navigate("/board");
    } catch (err) {
      console.error(err);
      if (err.response.status === 403) {
        alert("  .");
      } else {
        alert(" !");
      }
    }
  };

  const handleUpdate = async () => {
    if (!window.confirm(" ?")) {
      //->true, ->false
      return;
    }
    try {
      const res = await api.put(`/api/board/${id}`, { title, content });
      alert(" .");
      setPost(res.data); //   post   
      setEditing(false); //  
    } catch (err) {
      console.error(err);
      if (err.response.status === 403) {
        alert("  .");
      } else {
        alert(" !");
      }
    }
  };

  //    !
  const [newComment, setNewComment] = useState(""); //   
  const [comments, setComments] = useState([]); //    
  const [editingCommentContent, setEditingCommentContent] = useState("");
  const [editingCommentId, setEditingCommentId] = useState(null);
  const [commentErrors, setCommentErrors] = useState({});

  // format  ->   
  const formatDate = (dateString) => {
    return dateString.substring(0, 10);
  };

  //  ->  id  
  const handleCommentSubmit = async (e) => {
    //   
    e.preventDefault();
    setCommentErrors({});

    if (!user) {
      alert("   ");
    }
    if (!newComment.trim()) {
      alert("  .");
      return;
    }
    try {
      alert(" ?");
      await api.post(`/api/comments/${id}`, { content: newComment });
      setNewComment("");
      //   
      loadComments(); //     
    } catch (err) {
      if (err.response && err.response.status === 400) {
        setCommentErrors(err.response.data);
      } else {
        console.error(err);
        alert("  !");
      }
    }
  };

  //   
  const loadComments = async () => {
    try {
      const res = await api.get(`/api/comments/${id}`);
      //res->  (ex:7   4 )
      setComments(res.data);
    } catch (err) {
      console.error(err);
      alert("   !");
    }
  };

  //   
  const handleCommentDelete = async (commentId) => {
    if (!window.confirm(" ?")) {
      //->true, ->false
      return;
    }
    try {
      await api.delete(`/api/comments/${commentId}`);
      alert("  ");
      loadComments(); //   
    } catch (err) {
      console.error(err);
      alert(" !");
    }
  };

  //   ->  
  const handleCommentUpdate = async (commentId) => {
    try {
      await api.put(`/api/comments/${commentId}`, {
        content: editingCommentContent,
      });
      setEditingCommentId(null);
      setEditingCommentContent("");
      loadComments();
    } catch (err) {
      alert("  !");
    }
  };

  //   
  const handleCommentEdit = (comment) => {
    setEditingCommentId(comment.id);
    setEditingCommentContent(comment.content); //  
  };

  //    !

  if (loading) return <p>  ....</p>;
  if (error) return <p style={{ color: "red" }}>{error}</p>;
  if (!post)
    return <p style={{ color: "blue" }}>   .</p>;

  //       ->
  const isAuthor = user && user === post.author.username;

  return (
    <div className="detail-container">
      {editing ? (
        <div className="edit-form">
          <h2> </h2>
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
          <textarea
            value={content}
            onChange={(e) => setContent(e.target.value)}
          />
          <div className="button-group">
            <button className="edit-button" onClick={handleUpdate}>
              
            </button>
            <button className="delete-button" onClick={() => setEditing(false)}>
              
            </button>
          </div>
        </div>
      ) : (
        <>
          <h2>{post.title}</h2>
          <p className="author"> : {post.author.username}</p>
          <div className="content">{post.content}</div>

          <div className="button-group">
            <button className="list-button" onClick={() => navigate("/board")}>
              
            </button>

            {/*        */}
            {isAuthor && (
              <>
                <button
                  className="edit-button"
                  onClick={() => setEditing(true)}
                >
                  
                </button>
                <button className="delete-button" onClick={handleDelete}>
                  
                </button>
              </>
            )}
          </div>

          {/*   ! */}
          <div className="comment-section">
            {/*    ! */}
            <h3> </h3>
            <form onSubmit={handleCommentSubmit} className="comment-form">
              <textarea
                placeholder=" ."
                value={newComment}
                onChange={(e) => setNewComment(e.target.value)}
              />
              {commentErrors.content && (
                <p style={{ color: "red" }}>{commentErrors.content}</p>
              )}
              <button type="submit" className="comment-button">
                
              </button>
            </form>
            {/*    ! */}

            {/*    ! */}
            <ul className="comment-list">
              {comments.length === 0 && (
                <p style={{ color: "blue" }}>  </p>
              )}
              {comments.map((c) => (
                <li key={c.id} className="comment-item">
                  <div className="comment-header">
                    <span className="comment-author">{c.author.username}</span>
                    <span className="comment-date">
                      {formatDate(c.createDate)}
                    </span>
                  </div>

                  {editingCommentId === c.id ? (
                    /*    ! */
                    <>
                      <textarea
                        value={editingCommentContent}
                        onChange={(e) =>
                          setEditingCommentContent(e.target.value)
                        }
                      />
                      <button
                        className="comment-save"
                        onClick={() => handleCommentUpdate(c.id)}
                      >
                        
                      </button>
                      <button
                        className="comment-cancel"
                        onClick={() => setEditingCommentId(null)}
                      >
                        
                      </button>
                    </>
                  ) : (
                    /*    ! */
                    /*    ! */
                    <>
                      <div className="comment-content">{c.content}</div>

                      <div className="button-group">
                        {/*         */}
                        {user === c.author.username && (
                          <>
                            <button
                              className="edit-button"
                              onClick={() => handleCommentEdit(c)}
                            >
                              
                            </button>
                            <button
                              className="delete-button"
                              onClick={() => handleCommentDelete(c.id)}
                            >
                              
                            </button>
                          </>
                        )}
                      </div>
                    </>
                    /*    ! */
                  )}
                </li>
              ))}
            </ul>
            {/*    ! */}
          </div>
          {/*   ! */}
        </>
      )}
    </div>
  );
}

export default BoardDetail;
